.page-footer {
	height: 613px;
	width: $width-mobile;

	margin: 0 auto;
}

.feedback__wrapper {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.feedback__item {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.feedback__title {
	@include font-style (24px, auto, 300);
	color: $main-color;
}

.feedback__list-title {
	@include font-style (18px, auto, 700);
	color: $main-color;
}

.feedback__description {
	@include font-style (24px, auto, 300);
	color: #d22856;
	text-align: center;
	margin: 0;
}

.page-footer__map {
	margin-top: 60px;
	height: 166px;
	background-image: url(../img/map.jpg);
	background-repeat: no-repeat;
	background-position: bottom;
}

.page-footer__map::before {
  content: '';
  display: block;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 32px 160px 0 160px;
	border-color: $white transparent transparent transparent;
}
