.page-header {
  background-color: #283645;
  background-image: url(../img/iphone-5-in-hand-png.jpg);
  background-position: 0 0;
  background-repeat: no-repeat;
  min-height: 462px;
  width: 320px;
  margin: 0 auto; }

.page-header__logo-image {
  margin-top: 22px;
  margin-left: 20px; }

.page-header__wrapper {
  display: flex;
  justify-content: space-between;
  min-height: 66px;
  background-color: rgba(0, 0, 0, 0.3); }

.page-header__toggle {
  padding: 0;
  border: none;
  width: 50px;
  height: 24px;
  background: url(../img/menu-burger.png) no-repeat;
  background-color: #283645;
  margin-top: 24px;
  margin-right: 20px;
  cursor: pointer; }

.main-nav__list {
  display: flex;
  flex-direction: column; }

.site-list__item {
  display: none;
  height: 63px;
  background-color: #283645;
  text-align: center;
  font-size: 18px;
  line-height: 32px;
  font-weight: 700;
  text-transform: uppercase;
  color: #ffffff;
  box-shadow: 0 1px 0 #000000, inset 0 1px 0 rgba(255, 255, 255, 0.15);
  margin-top: 1px; }

.download {
  width: 320px;
  min-height: 271px;
  margin: 0 auto;
  background-color: #1d2631; }

.download__button {
  display: inline-block;
  width: 281px;
  height: 55px;
  text-align: center;
  padding-top: 15px;
  font-size: 18px;
  line-height: 18px;
  font-weight: 700;
  color: #ffffff;
  border: 3px solid #ffffff;
  border-radius: 50px;
  margin-top: 28px;
  margin-left: 19px;
  margin-bottom: 32px; }

.download__wrapper {
  display: flex;
  align-items: center;
  justify-content: center; }

.download__list {
  width: 205px;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.download__link {
  display: block; }
  .download__link--appstore {
    background-image: url(../img/apple-logo.png);
    width: 47px;
    height: 56px; }
  .download__link--play-market {
    background-image: url(../img/android-logo.png);
    width: 43px;
    height: 47px; }
  .download__link--microsoft-store {
    background-image: url(../img/microsoft-logo.png);
    width: 44px;
    height: 41px; }

.download__discription {
  font-size: 14px;
  line-height: 24px;
  font-weight: 300;
  color: #ffffff;
  text-align: center; }

.features {
  height: 1002px;
  width: 320px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  background-color: #f2f2f2;
  background-image: url(../img/iphone_screen.png);
  background-repeat: no-repeat;
  background-position: bottom -42px right 82px; }

.features__slogan-wrapper {
  height: 217px;
  background-color: #ffffff; }

.features__list-wrapper::before {
  content: '';
  display: block;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 32px 320px 0 0;
  border-color: #ffffff #f2f2f2 transparent transparent; }

.features__slogan {
  font-size: 24px;
  line-height: auto;
  font-weight: 300;
  text-align: center; }

.features__item {
  text-align: center; }

.features__title {
  font-size: 18px;
  line-height: 24px;
  font-weight: 700; }

.features__description {
  font-size: 14px;
  line-height: 24px;
  font-weight: 400; }

.review {
  height: 352px;
  width: 320px;
  margin: 0 auto; }

.review__item {
  margin: 0;
  display: none; }
  .review__item--active {
    display: flex;
    flex-direction: column;
    align-items: center; }

.review__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center; }

.review__author-name {
  font-style: normal;
  font-size: 18px;
  line-height: 30px;
  font-weight: 700; }

.review__quote {
  font-size: 14px;
  line-height: 30px;
  font-weight: 400;
  text-align: center; }

.review__buttons {
  display: none; }

.slider__toggles {
  width: 62px;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.slider__toggle {
  box-sizing: content-box;
  width: 6px;
  height: 6px;
  padding: 0;
  border: none;
  border-radius: 3px;
  background-color: #bec2c7;
  cursor: pointer; }
  .slider__toggle--active {
    border: 3px solid #283645;
    border-radius: 6px;
    background-color: #ffffff; }

.tariff::before {
  content: '';
  display: block;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 32px 320px 0 0;
  border-color: #ffffff #f2f2f2 transparent transparent; }

.tariff {
  width: 320px;
  height: 445px;
  margin: 0 auto;
  background-color: #f2f2f2; }

.tariff__wrapper {
  margin-top: 66px;
  display: flex;
  flex-direction: column;
  align-items: center; }

.tariff__table {
  border-collapse: collapse; }

.table__cell {
  display: none;
  width: 276px;
  height: 59px;
  background-color: #ffffff;
  border: 2px solid #e5e5e5; }
  .table__cell--top {
    height: 94px;
    background-color: #283645; }
  .table__cell--visible {
    display: block; }

.table__content--top-name {
  font-size: 18px;
  line-height: 16px;
  font-weight: 700;
  color: #ffffff;
  text-transform: uppercase;
  text-align: center; }

.table__content--top-price {
  font-size: 24px;
  line-height: 0;
  font-weight: 300;
  color: #ffffff;
  text-align: center; }

.table__content--feature {
  font-size: 14px;
  line-height: 30px;
  font-weight: 400;
  text-transform: uppercase;
  padding-left: 22px; }

.table__content--avail {
  background-image: url(../img/yes.png);
  background-repeat: no-repeat;
  background-position-x: 230px; }

.table__content--no-avail {
  background-image: url(../img/no.png);
  background-repeat: no-repeat;
  background-position-x: 230px; }

.page-footer {
  height: 613px;
  width: 320px;
  margin: 0 auto; }

.feedback__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center; }

.feedback__item {
  display: flex;
  flex-direction: column;
  align-items: center; }

.feedback__title {
  font-size: 24px;
  line-height: auto;
  font-weight: 300;
  color: #283645; }

.feedback__list-title {
  font-size: 18px;
  line-height: auto;
  font-weight: 700;
  color: #283645; }

.feedback__description {
  font-size: 24px;
  line-height: auto;
  font-weight: 300;
  color: #d22856;
  text-align: center;
  margin: 0; }

.page-footer__map {
  margin-top: 60px;
  height: 166px;
  background-image: url(../img/map.jpg);
  background-repeat: no-repeat;
  background-position: bottom; }

.page-footer__map::before {
  content: '';
  display: block;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 32px 160px 0 160px;
  border-color: #ffffff transparent transparent transparent; }
