.tariff::before {
  content: '';
  display: block;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 32px 320px 0 0;
	border-color: $white $grey transparent transparent;
}

.tariff {
	width: $width-mobile;
	height: 445px;

	margin: 0 auto;
	
	background-color: $grey;
}

.tariff__wrapper {
margin-top: 66px;
display: flex;
flex-direction: column;
align-items: center;
}

.tariff__table {
	border-collapse: collapse;
}

.table__cell {
	display: none;
	width: 276px;
	height: 59px;
	background-color: $white;

	border: 2px solid #e5e5e5;

	&--top {
		height: 94px;
		background-color: $main-color;
	}

	&--visible {
		display: block;
	}
}

.table__content {
	&--top-name {
		@include font-style (18px, 16px, 700);
		color: $white;
		text-transform: uppercase;
		text-align: center;
	}

	&--top-price {
		@include font-style (24px, 0, 300);
		color: $white;
		text-align: center;
	}

	&--feature {
		@include font-style (14px, 30px);
		text-transform: uppercase;
		padding-left: 22px;
	}

	&--avail {
		background-image: url(../img/yes.png);
		background-repeat: no-repeat;
		background-position-x: 230px;
	}

	&--no-avail {
		background-image: url(../img/no.png);
		background-repeat: no-repeat;
		background-position-x: 230px;
	}
}