.page-header { 
	background-color: $main-color;
	background-image: url(../img/iphone-5-in-hand-png.jpg);
	background-position: 0 0;
	background-repeat: no-repeat;

	min-height: 462px;
	width: $width-mobile;

	margin: 0 auto;
}

.page-header__logo-image {
	margin-top: 22px;
	margin-left: 20px;
}

.page-header__wrapper {
	display: flex;
	justify-content: space-between;
	min-height: 66px;
	background-color: transparentize($black,.7);
}

.page-header__toggle {
	padding: 0;
	border: none;
	
	width: 50px;
	height: 24px;

	background: url(../img/menu-burger.png) no-repeat;
	background-color: $main-color;

	margin-top: 24px;
	margin-right: 20px;

	cursor: pointer;
}

.main-nav__list {
	display: flex;
	flex-direction: column;
}

.site-list__item {
	display: none;
	height: 63px;
	background-color: $main-color;

	text-align: center;

	@include font-style(18px, 32px, 700);
	text-transform: uppercase;
	color: $white;

	box-shadow: 0 1px 0 $black, inset 0 1px 0 transparentize($white,.85);	

	margin-top: 1px;
}
