
.download {
	width: $width-mobile;
	min-height: 271px;

	margin: 0 auto;
	background-color: $deep-blue;
}

.download__button {	
	display: inline-block;
	width: 281px;
	height: 55px;

	text-align: center;
	padding-top: 15px;

	@include font-style(18px, 18px, 700);
	color: $white;


	border: 3px solid $white;
	border-radius: 50px;

	margin-top: 28px;
	margin-left: 19px;
	margin-bottom: 32px;
}

.download__wrapper {
	display: flex;
  align-items: center;
	justify-content: center;
}	

.download__list {
	width: 205px;

	display: flex;
	justify-content: space-between;
	align-items: center;

}

.download__link {
	display: block;

	&--appstore {
		background-image: url(../img/apple-logo.png);
		width: 47px;
		height: 56px;
	}
	
	&--play-market {
		background-image: url(../img/android-logo.png);
		width: 43px;
		height: 47px;
	}
	
	&--microsoft-store {
		background-image: url(../img/microsoft-logo.png);
		width: 44px;
		height: 41px;
	}

}

.download__discription {
	@include font-style(14px, 24px, 300);
	color: $white;
	text-align: center;
}