.features {
	height: 1002px;	
	width: $width-mobile;

	margin: 0 auto;

	display: flex;
	flex-direction: column;

	background-color: $grey;
	background-image: url(../img/iphone_screen.png);
	background-repeat: no-repeat;
	background-position: bottom -42px right 82px;
}

.features__slogan-wrapper {
	height: 217px;
	background-color: $white;
}

.features__list-wrapper::before {
  content: '';
  display: block;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 32px 320px 0 0;
	border-color: $white $grey transparent transparent;
}

.features__slogan {
	@include font-style(24px, auto, 300);
	text-align: center;
}

.features__item {
	text-align: center;
}

.features__title {
	@include font-style(18px, 24px, 700);
}

.features__description {
	@include font-style(14px, 24px, 400);
}