@mixin font-style ($font-size, $line-height, $font-weight: 400) {
	font-size: $font-size;
	line-height: $line-height;
	font-weight: $font-weight;
}

$font: "Open Sans", "Arial", sans-serif;
$main-color: #283645;
$deep-blue: #1d2631;
$black: #000000;
$white: #ffffff;
$grey: #f2f2f2;
$width-mobile: 320px;
$width-tablet: 768px;
$width-desktop: 1200px;

html {
  box-sizing: border-box;
}
*, *:before, *:after {
  box-sizing: inherit;
}

body {
	min-width: 320px;

	margin: 0;
	padding: 0;

	font-family: $font;

	@include font-style (14px, 24px);
}

a {
  text-decoration: none;
}

.site-list {
	list-style: none;
  margin: 0;
  padding: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0)
}