.review {
	height: 352px;
	width: $width-mobile;

	margin: 0 auto;
}

.review__item {
	margin: 0;
	display: none;

	&--active {
		display: flex;
		flex-direction: column;
		align-items: center;
	}

}

.review__wrapper {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.review__author-name {
	font-style: normal;
	@include font-style (18px, 30px, 700);
}

.review__quote {
	@include font-style (14px, 30px);
	text-align: center;
}

.review__buttons {
	display: none;
}

.slider__toggles {
	width: 62px;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.slider__toggle {
	box-sizing: content-box;
	width: 6px;
	height: 6px;
	padding: 0;

	border: none;
	border-radius: 3px;
	
	background-color: #bec2c7;

	cursor: pointer;

	&--active {
		border: 3px solid #283645;
		border-radius: 6px;
		background-color: $white;
	}

}